.text-center {
  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  user-select: none;
  /* no permite seleccionar nada */
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lora", serif;
  font-size: 1.6rem;
}

p {
  color: #FFFFFF;
  font-size: 2.5rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Lora", serif;
  margin: 0 0 5rem/2 0;
  font-weight: 700;
  color: #3aa0f3;
  text-align: center;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  color: #FFFFFF;
}

body {
  background-color: #1a1b15;
}

@media (min-width: 768px) {
  .contenedor-barberia {
    /* cuando no este dentro de este tamaño va a ser mobile first */
    height: 100vh;
    /* ocupa todo el viewport */
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-barberia .imagen {
  height: 30rem;
  background-image: url("../img/1.jpg");
  background-size: cover;
  /* para tomar todos los espacios */
  background-position: center center;
  /* para centrarse en todos los lados */
}
@media (min-width: 768px) {
  .contenedor-barberia .imagen {
    height: auto;
    /* para tomar todos los espacios */
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding-top: 2.5rem;
}
.app .seccion {
  display: none;
  /* Oculta los servivios */
  margin-top: 1.6666666667rem;
}

.mostrar-seccion {
  display: block !important;
}

.tabs {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.tabs button {
  background-color: #FFFFFF;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
  border: none;
  transition: all 0.5s;
}
.tabs button.actual {
  background-color: #3aa0f3;
  color: #FFFFFF;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #3aa0f3;
  color: #FFFFFF;
  box-shadow: 0 0 0.5rem 0.1px #79cef5;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.servicio {
  width: 100%;
  height: 95%;
  margin-bottom: 2rem;
  background-color: #FFFFFF;
  padding: 2.5rem;
  border-radius: 1rem;
  transition: box-shadow 0.3s ease-in;
}
@media (min-width: 768px) {
  .servicio {
    margin-bottom: 0;
  }
}
.servicio p {
  font-size: 2rem;
  margin: 0 0 1.25rem 0;
}
.servicio .nombre-servicio {
  color: #1a1b15;
}
.servicio .precio-servicio {
  color: #3aa0f3;
  margin: 0;
  font-size: 3rem;
  font-weight: 600;
}
.servicio:hover.servicio {
  cursor: pointer;
  box-shadow: 1px 3px 6px 0px rgba(18, 167, 189, 0.96);
  -webkit-box-shadow: 1px 3px 6px 0px rgba(18, 167, 189, 0.96);
  -moz-box-shadow: 1px 3px 6px 0px rgba(18, 167, 189, 0.96);
}
.servicio.seleccionado {
  background-color: #3aa0f3;
}
.servicio.seleccionado p {
  color: #FFFFFF;
}

.paginacion {
  display: flex;
  justify-content: space-between;
  padding: 5rem 0;
}
.paginacion button {
  background-color: #3aa0f3;
  padding: 0.5rem 1rem;
  text-align: center;
  color: #FFFFFF;
  border: none;
  font-weight: 700;
  font-size: 2.5rem;
}
.paginacion button.ocultar {
  visibility: hidden;
}
.paginacion button:hover {
  cursor: pointer;
}

.formulario {
  width: 100%;
  margin-top: 5rem;
}

.campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.campo label {
  flex: 0 0 10rem;
  color: #FFFFFF;
  font-size: 2rem;
}
.campo input {
  flex: 1;
  padding: 1rem;
  border-radius: 1rem;
  border: none;
  font-size: 2rem;
}

.alerta {
  padding: 1.6666666667rem;
  text-align: center;
  color: #FFFFFF;
  width: 100%;
  display: block;
  border-radius: 10px;
}
.alerta.error {
  background-color: #c70000;
}

.invalidar-cita {
  text-align: center;
  margin: 2.5rem 0;
  text-transform: uppercase;
}

.contenido-resumen {
  margin-bottom: 2.5rem 0;
}
.contenido-resumen p {
  margin-bottom: 0.5rem;
}
.contenido-resumen p span {
  font-weight: 600;
  color: #3aa0f3;
  font-size: 2.4rem;
}
.contenido-resumen .resumen-servicios h3 {
  margin: 3.3333333333rem 0;
}
.contenido-resumen .resumen-servicios .contenedor-servicio {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid white;
  display: flex;
  justify-content: space-between;
}
.contenido-resumen .resumen-servicios .contenedor-servicio .precio {
  margin-right: 0;
  font-weight: 600;
  color: #3aa0f3;
  font-size: 2.4rem;
}

.total {
  text-align: right;
}