html {
    font-size: 62.5%;
    box-sizing: border-box;
    user-select: none; /* no permite seleccionar nada */
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
}
p {
    color: $blanco;
    font-size: 2.5rem;
}

.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

h1, h2, h3 {
    font-family: $fuente_principal;
    margin: 0 0 5rem / 2 0 ;
    font-weight: $black;
    color: $azul;
    text-align: center;
}

h1 {
    font-size: 5rem;
}
h2 {
    font-size: 4rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
h4 {
    color: $blanco;
}