.tabs {
    @include grid(3, 2rem);
    button {
        background-color: $blanco;
        font-size: 1.5rem;
        font-weight: $black;
        padding: 1rem;
        text-align: center;
        text-transform: uppercase;
        border: none;
        transition: all .5s;

        &.actual {
            background-color: $azul;
            color: $blanco;
        }
        &:hover {
            cursor: pointer;
            background-color: $azul;
            color: $blanco;
            box-shadow:  0 0 .5rem .1px rgb(121, 206, 245);
        }
    }
}
