.formulario {
    width: 100%;
    margin-top: 5rem;
}

.campo {
    display: flex;
    align-items: center; // alinea verticalmente los labels
    margin-bottom: 2rem;

    label {
        flex: 0 0 10rem; // flex distribye el factor de crecimiento para cada elemento
        color: $blanco;
        font-size: 2rem;
    }
    input {
        flex: 1; // cantidad de pixeles que se le va a dar a este elemento
        padding: $separacion / 5;
        border-radius: 1rem;
        border: none;    
        font-size: 2rem;
    }
}

.alerta {
    padding: $separacion / 3;
    text-align: center;
    color: $blanco;
    width: 100%;
    display: block;
    border-radius: 10px;

    &.error {
        background-color: rgb(199, 0, 0);
    }
}