body {
    background-color: $negro; 
}

.contenedor-barberia {
    @include tablet { /* cuando no este dentro de este tamaño va a ser mobile first */
        height: 100vh; /* ocupa todo el viewport */
        @include grid(2, $separacion / 2);
    }
    .imagen {
        height: 30rem;
        background-image: url('../img/1.jpg');
        background-size: cover; /* para tomar todos los espacios */
        background-position: center center; /* para centrarse en todos los lados */
        @include tablet {
            height: auto; /* para tomar todos los espacios */
        }
    }
}

.app {
    width: 95%;
    margin: 0 auto;
    padding-top: $separacion / 2;

    .seccion {
        display: none; /* Oculta los servivios */
        margin-top: $separacion / 3;
    }
}

.mostrar-seccion {
    display: block!important; // muestra la seccion cuando de damos click
}
