.listado-servicios {
    @include tablet { // cuando no este en un tamaño pequeño se ba a ver con dos columnas
        @include grid ( 3, 2rem);
    }
}

.servicio {
    width:100%;
    height: 95%;
    margin-bottom: 2rem;
    background-color: $blanco;
    padding: $separacion / 2;
    border-radius: 1rem;
    transition: box-shadow .3s ease-in;

    @include tablet { // cuando no este en pequeño no va a tener ese margin bottom
        margin-bottom: 0;
    }

    p {
        font-size: 2rem;
        margin: 0 0 $separacion / 4 0;
    }

    .nombre-servicio {
        color: $negro;
    }

    .precio-servicio {
        color: $azul;
        margin: 0;
        font-size: 3rem;
        font-weight: $bold;
    }

    &:hover.servicio {
        cursor: pointer;
        box-shadow: 1px 3px 6px 0px rgba(18,167,189,0.96);
        -webkit-box-shadow: 1px 3px 6px 0px rgba(18,167,189,0.96);
        -moz-box-shadow: 1px 3px 6px 0px rgba(18,167,189,0.96);
    }

    &.seleccionado {
        background-color: $azul;

        p {
            color: $blanco;
        }
    }  
}
