.paginacion {
    display: flex;
    justify-content: space-between; // manda uno al lado izuierdo y ptro al lado derecho
    padding: $separacion 0 ;

    button { 
        background-color: $azul;
        padding: .5rem 1rem;
        text-align: center;
        color: $blanco;
        border: none;
        font-weight: $black;
        font-size: 2.5rem;

        // si esta el boton con la clase de opcultar
        &.ocultar {
            visibility: hidden; // para mantener la posicion pero sin que se vea
        }
        &:hover {
            cursor: pointer;
        }
    }
}